<template>
<div id="images_app">
    <ul v-for="(i, index) in images" v-bind:key="index">
      <li>{{ i.id }}</li>
      <li>{{ i.name }}</li>
      <li><img :src= "i.image" :alt="'not image' + index" class="img-thumbnail"/></li>
    </ul>

    <ul v-for="(i, index) in imgURL" v-bind:key="index">
      <li><img :src= "i" :alt="'not image' + index" /></li>
    </ul>
</div>
</template>

<script>
import axios from "axios";

export default {
  name: "images_app",
  data() {
    return {
      images: [],
      imgURL: ['https://sun9-44.userapi.com/s/v1/if1/qw3vWR63rnWIPexrEErujILvop-GpxX8MJRJx1emFNrgy2Ve9Hf3sqh5NLHETJNtyNMxaiTe.jpg?size=200x0&quality=96&crop=18,0,586,594&ava=1']
      };
  },
  async created() {
    try {
      const res = await axios.get('https://alotos.pythonanywhere.com/api/products/',);
      this.images = res.data;
    }catch (e) {
      console.error(e);
    }
  },
};
</script>
