import axios from "axios";

export default {
  GET_PRODUCTS_FROM_API({commit}) {
    return axios('https://alotos.pythonanywhere.com/api/products/', {
      method: "GET"
    })
      .then((products) => {
        commit('SET_PRODUCTS_TO_STATE', products.data);
        return products;
      })
      .catch((error) => {
        console.log(error)
        return error;
      })
  },

  GET_CATEGORYES_FROM_API({commit}) {
    return axios('https://alotos.pythonanywhere.com/api/categoryes/', {
      method: "GET"
    })
      .then((categoryes) => {
        commit('SET_CATEGORYES_TO_STATE', categoryes.data);
        return categoryes;
      })
      .catch((error) => {
        console.log(error)
        return error;
      })
  },

  GET_AUTH_FROM_API({commit}) {
    return axios('https://localhost:3000/authUser', {
      method: "POST",
      data: {
        login: "",
        password: ""
      }
    })
      .then((authUser) => {
        commit('SET_AUTH_TO_STATE', authUser.data);
        return authUser;
      })
      .catch((error) => {
        console.log(error)
        return error;
      })
  },


  GET_CONTACTS_FROM_API({commit}) {
    return axios('https://alotos.pythonanywhere.com/api/contactspage/', {
      method: "GET"
    })
      .then((contacts) => {
        commit('SET_CONTACTS_TO_STATE', contacts.data);
        return contacts;
      })
      .catch((error) => {
        console.log(error)
        return error;
      })
  },

  GET_CONTENT_FROM_API({commit}) {
    return axios('https://alotos.pythonanywhere.com/api/mainpage/', {
      method: "GET"
    })
      .then((content) => {
        commit('SET_CONTENT_TO_STATE', content.data);
        return content;
      })
      .catch((error) => {
        console.log(error)
        return error;
      })
  },
}
