<template>
  <div class='v-contacts'>
    <h1>Наши контакты</h1>
    <ul v-for="(i, index) in contacts" v-bind:key="index">
      <span>{{ i.title|safe}}</span>
      <span v-html="i.text"></span>
    </ul>

  </div>
</template>






<script>
import {mapActions, mapGetters} from 'vuex'


  export default {
    name: "v-contacts",
    props: {},
    data() {
      return {
        contacts: [],
      }
    },
    computed: {
      ...mapGetters([
        'CONTACTS',])
    },

    methods: {
      ...mapActions([
        'GET_CONTACTS_FROM_API',
      ]),

      allContacts() {
        let contacts = this.$store.state.contacts
        this.contacts = contacts
        let title = this.contacts.title
        let text = this.contacts.text
        return this.contacts
      },
    },
    mounted() {
      this.GET_CONTACTS_FROM_API()
        .then((response) => {
          if (response.data) {
            this.allContacts()
          }
        })
    }

  }
</script>

