<template>
  <div>

    <b-carousel
    id="carousel-fade"
    style="text-shadow: 0px 0px 2px #000"
    fade
    indicators
    img-width="1024"
    img-height="480"
  >


      <b-carousel-slide  v-for="(i, index) in slides" v-bind:key="index">
        <template #img>
          <img
            class="d-block img-fluid w-100"
            width="1024"
            height="480"
            :src= "i.image" :alt="'not image' + index"
          >
          </template>
      </b-carousel-slide>
    </b-carousel>

  </div>
</template>

<script>
import axios from "axios";
  export default {
    data() {
      return {
        slide: 0,
        sliding: null,
        slides: []
      }
    },
    async created() {
      try {
        const res = await axios.get('https://alotos.pythonanywhere.com/api/slider/',);
        this.slides = res.data;
      }catch (e) {
        console.error(e);
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      }
    }
  }
</script>

<style>
 .slider-1 {
   z-index: auto;
 }
</style>
