import Vue from 'vue'
import Router from 'vue-router'

import vCatalog from '../components/catalog/v-catalog'
import vCart from '../components/cart/v-cart'
import vMainPage from '../components/main-page/v-main-page'
import vProductPage from '../components/catalog/v-product-page'
import vContacts from '../components/contacts/v-contacts'

import vLogin from '../components/loginsys/login'

import vOnlytest from '../components/testing/onlytest'

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'mainPage',
      component: vMainPage
    },
    {
      path: '/catalog',
      name: 'catalog',
      component: vCatalog
    },
    {
      path: '/product',
      name: 'product',
      component: vProductPage
    },
    {
      path: '/cart',
      name: 'cart',
      component: vCart,
      props: true
    },
    {
      path: '/ourContacts',
      name: 'ourContacts',
      component: vContacts
    },
    {
      path: '/auth',
      name: 'auth',
      component: vLogin
    },
    {
      path: '/test',
      name: 'test',
      component: vOnlytest
    },
  ]
})

export default router;
