      ]),

      allCatigoryes() {
        let categories = this.$store.state.categoryes
        this.categories = categories
        let name = this.categories.name
        let value = this.categories.value
        return this.categories
      },


      allContent() {
        let content = this.$store.state.content
        this.content = content
        let title = this.content.title
        let text = this.content.text
        return this.content
      },
    },

    mounted() {
      this.GET_CATEGORYES_FROM_API()
      this.GET_CONTENT_FROM_API()
        .then((response) => {
          if (response.data) {
            this.allCatigoryes()
            this.allContent()
          }
        })
    }
  }
</script>

